.me{
    width: 25rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 12rem);
    margin-top: -1rem;
    border-radius: 1rem 1rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* ********* Scroll Down ********** */

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-size: 0.9rem;
    font-weight: 300;
}

/*  *******MEDIA QUERIES FOR RESPONSIVENESS ******* */

@media screen and (max-width: 1024px){

   header{
    height: 68vh;
   }

}

@media screen and (max-width: 600px){

   header{
    height: 100vh;
   }

   .header__socials,
   .scroll__down{
    display: none;
   }
}

